import React from 'react';
import logo from './react.svg';
import axios from 'axios';
import { Input, Icon, Layout, Row, Col, Button, Typography } from 'antd';
import autobind from 'autobind-decorator'

const { TextArea  } = Input;
const { Text } = Typography;
const { Content, Header } = Layout;

import './Home.css';

class Home extends React.Component {
	state = {
		source: '',
		index: '',
		query: '',
		change: ''
	}
	@autobind
	changeItem(key, val) {
		let obj = {};
		obj[key] = val;
		this.setState(obj)
	}
	@autobind
	createSample() {
		axios.post('/run', {
			...this.state
		}).then(res => console.log(res))
	}
	@autobind
	queryData() {
		axios.post('/query', {
			index: this.state.index,
			query: this.state.query
		}).then(res => {
			if (res.data.data) {
				this.setState({
					source: res.data.data
				})
			}
		})
	}
  render() {
	  const { index, query, source, change } = this.state;
    return (
      <div className="Home">
			<Layout>
				<Header className="header">
					<Text type="warning">造数据</Text>
				</Header>
			  <Content  style={{ padding: '15px'  }}>
					<Row>
						  <Col span={6}>
							  <Input placehoder="输入索引名称" value={index} onChange={(e) => this.changeItem("index", e.target.value)}/>
							  <TextArea placeholder="输入查询语句" autosize={{minRows: 4, maxRows: 6}} onChange={(e) => this.changeItem("query", e.target.value)} onBlur={this.queryData}/>
						</Col>
					  <Col span={18}>
						  <TextArea autosize={{ minRows: 8, maxRows: 50 }} value={source} onChange={(e) => this.changeItem("source", e.target.value)}/>
						  <TextArea autosize={{minRows: 8, maxRows: 20}} onChange={(e) => this.changeItem("change", e.target.value)} value={change}/>
						<Button type="primary" onClick={this.createSample}>生成样例</Button>
						<Button type="danger">开始创建</Button>
					</Col>
					</Row>

			  </Content>
			</Layout>
      </div>
    );
  }
}

export default Home;
